
#tecnologia-content{
    display: flex;
    justify-content: center;
    margin: 0 auto;
    margin: 5%;
}

#tecnologias-list{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    
    max-width: 1000px;
}

#tecnologia-content .tecnologia-item{
    margin: 10px;
}


@media only screen and (max-width: 800px) {
    #tecnologias-list{
        justify-content: flex-start;
        flex-direction: row;
        flex-wrap: nowrap;
        overflow-x: auto;
        margin: 0 auto;
    }
}