nav {
  margin: 1% 5%;  
  display: flex;
  justify-content: space-between;
  align-items: center;
}
nav ul {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
}

nav li {
  margin: 1rem;
}

nav h1 {
  display: inline;
  font-weight: normal;
}

.hide{
  display: none;
}

@media only screen and (max-width: 800px)  {
  nav ul li{
      display: none;
  }
  .hamburger{
    display: inline;
  }
}

