

#sobre_mim{
    max-width: 1024px;
    overflow: hidden;
    /* border: 1px solid #000000;
    border-radius: 10px; */
}

#sobre_mim_content{
    display: flex;
    justify-content: center;
}

#sobre_mim_content #description{
    width:40%;
    margin:4rem 1rem;
}

#sobre_mim_content #profile_photo{
    margin:2rem;
    border-radius: 50px;
    height: 50%;
    width: 40%;   
    transition: 1s ease; 
}

#sobre_mim_content #profile_photo:hover{
    transition: 1s ease;
    transform: scale(1.05);
}

.social{
    display: flex;
    justify-content: center;
}
@media only screen and (max-width: 800px)  {
    #sobre_mim_content{
        flex-wrap: wrap-reverse;
    }
    #sobre_mim_content #description{
        margin: 1rem;
        width: 80%;
    }
    #sobre_mim_content #profile_photo{
        width: 80%;
        margin: 0 auto;
    }
  }

  #sobre_mim_content .contact-button{
     margin: 5% 0 ;
 }