.line{
    width: 8px;
    margin: 0 42px;
    background-color: #1a1a1a;
    border-radius: 5px;
}
.form_academica div {
    display: flex;
}
.form_academica
{
    margin: 10%;
}

.form_academica div svg {
    min-width: 30px;
    max-width: 30px;
}
.form_academica_content {
    width: 100%;
    display: flex;
    flex-direction: column;
    transform: translateX(-60px);
}

.form_academica_content div {
    width: 100%;
    display: flex;
}
.form_academica_content div div{
    padding: 8px 2% ;
    display: flex;
    flex-direction: column;
}

.form_academica_content div SVG {
    background-color: transparent;
}

.form_academica_content div p {
    word-break: break-word;
}

.form_academica_content div div div{
    flex-direction: row;
    align-items: center;
    width: 100%;
}
.form_academica_content h3{
    margin-top: 10px;
    margin-bottom: 8px;
}
.form_academica_content p{
    margin: 0;
}

.formacao-tecnologias {
    flex-wrap: wrap;
}

.formacao-tecnologias span{
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
}