.hamburger-menu{
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.hamburger-menu ul{
    list-style: none;
    margin: 0;
    padding: 0;
}

.hamburger-menu li{
    margin: 2rem;
    text-align: center;
}