#experiencias-profissionais-content{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 5% 0;
}



#experiencias-profissionais-content svg {
    transform: rotate(-60deg);
    animation: anim 400ms ease 0s infinite alternate;
  
    width:150px;
}

@keyframes anim{
    0%{
     transform: rotate(30deg);
    }
  }
  